
export const myTableMessages = {
    Search: ' ...',
    New: '',
    Edit: '',
    Cancel: '',
    Delete: '',
    Refresh: '',
    Save: ' ',
    YouAreGoingToDeleteRows: '    {count}   !',
    ChangesAreSaved: '{plural}:     ',
    RowsDeleted: '{plural}: {count}   ',
    RowsSelected: '{count}    ',

    format: (message, args) => {
        if (!message || !args) return message;
        for (var propName in args) {
            message = message.replace("{" + propName + "}", args[propName]);
        }
        return message;
    }
}
