import { Column, Table } from "./table";

export const menus = new Table({
    name: 'Menus',
    title: ' ',
    singularTitle: '',
    columns: [
        new Column({
            isPK: true,
            name: 'id',
            title: '',
            dir:"ltr",
        }),

        new Column({
            name: 'title',
            title: '',
            editor: 'check',
        }),

        new Column({
            name: 'sortOrder',
            title: '',
            editor: 'number',
            dir:"ltr",
        }),

        new Column({
            name: 'url',
            title: ' ',
            dir:"ltr",
        }),

        new Column({
            name: 'openInNewTab',
            title: '    ',
            editor: 'checkbox',
        }),
    ],
});

