import { Column, Table } from "./table";

export const applications = new Table({
    name: 'Applications',
    title: ' ',
    singularTitle: '',
    columns: [

        new Column({
            isPK: true,
            name: 'id',
            title: '',
            sortable: true,
            toggleOnClick: true,
            cellStyle: { cursor: 'default' },
            cellClassName: "text-primary",
            dir:"ltr",
        }),

        new Column({
            name: 'title',
            title: '',
            toggleOnClick: true,
            cellStyle: { cursor: 'default' },
        }),

    ],
    sortable: true,
    filterable: true,
});
